import React,{useState,useEffect,useRef} from 'react';
import M from 'materialize-css';
import axios from 'axios';
import { useSelector,useDispatch } from 'react-redux';
import Recipe from './recipe';


function Home() {

    const dispatch = useDispatch()

    var title = useRef('');
    var description = useRef('');
    const[recipies,setRecipies] = useState([]);
    const [query,setquery]=useState('');

    var storeRecipes = useSelector(state => state.recipies);

    useEffect(()=>{
        M.AutoInit();
        dispatch({type:'REFRESH'});
    },[])

    useEffect(()=>{
        setRecipies(storeRecipes);
    },[storeRecipes])

    const AddRecipeApi = () =>{
        var results =axios.post('https://recipe-app-0002.herokuapp.com/add/recipe', {
            title:title.current,description:description.current
        })
        .then(function (response) {
            dispatch({type:'REFRESH'});
            window.location.reload(false);
        })
        .catch(function (err) {
            alert("ERROR while posting recipe data")
        });
    }

    const search = () =>{
        let querystring = '?title=' + query ;
        var results =axios.get('https://recipe-app-0002.herokuapp.com/get/recipe' + querystring, {
        })
        .then(function (response) {
            setRecipies(response.data.recipies);
            setquery('');
        })
        .catch(function (err) {
            alert("ERROR while posting recipe data")
        });
    }

    return (
        <>
            <div class="App">
                <br/>
            <button class="waves-effect waves-light btn indigo darken-4 modal-trigger" style={{width:"750px"}} href="#modal1">Add Recipe</button>
            <br/><br/>

            <input type="text" placeholder="Search.." value={query} style={{width:"500px"}} onChange={(e)=>{setquery(e.target.value)}} /> &nbsp;&nbsp;&nbsp; <button class="btn blue inline" onClick={search}>Find</button>


            <br/><br/>
            </div>
            <div style={{marginLeft:"390px"}}>
            <Recipe recipeList = {recipies} />
            </div>
            
           


            <div id="modal1" class="modal" style={{width:"600px"}}>
                <div class="modal-content">
                    <h4>Recipe Details</h4>
                    <div class="row">
                        <div class="input-field col s8 ">
                            <input type="text" onChange={(e)=>{title.current=e.target.value}} />
                            <label>Recipe Name</label>
                        </div>
                    </div>
                    <div class="row">
                        <div class="input-field col s8 ">
                        <textarea onChange={(e)=>{description.current=e.target.value}} class="materialize-textarea"></textarea>
                        <label for="textarea1">Description</label>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                <a href="#!" class="modal-close blue waves-effect waves-green btn-flat" onClick={AddRecipeApi}>Add Recipe</a>
                </div>
            </div>
        </>
    )
}

export default Home
