import React,{useState,useEffect,useRef} from 'react';
import { useDispatch } from 'react-redux';
import M from 'materialize-css';
import axios from 'axios';

function Recipe ({recipeList}) {

    const[list,setlist] = useState([]);
    const dispatch = useDispatch()

    var current_recipe_id = useRef(null);
    const[title ,settitle]= useState('');
    const[description,setdesc] = useState('');
    
    useEffect(()=>{
        console.log("hello")
        M.AutoInit();
    },[])

    const DeleteAPI= () =>{
        console.log("trying to delete " +current_recipe_id.current)
        var results =axios.delete('https://recipe-app-0002.herokuapp.com/delete/recipe/' + current_recipe_id.current)
        .then(function (response) {
            dispatch({type:'REFRESH'});
        })
        .catch(function (err) {
            alert("ERROR while deleting recipe data")
        });
    }

    const EditAPI= () =>{

        console.log("edit api");
        console.log(title);
        var results =axios.put('https://recipe-app-0002.herokuapp.com/update/recipe/' + current_recipe_id.current ,{
            title,description
        })
        .then(function (response) {
            dispatch({type:'REFRESH'});
        })
        .catch(function (err) {
            console.log(err);
            alert("ERROR while updating recipe data")
           
        });
    }

    useEffect(()=>{
        function card(elem){
            let item = 
            <div class="row">
                <div class="col s8">
                <div class="card">
                    <div class="card-content">
                        <span class="card-title">{elem.title}</span>
                        <p>{elem.description}</p>
                    </div>
                    <div class="card-action">
                    <button class="btn red" onClick={()=>{current_recipe_id.current = elem.id; DeleteAPI();}}>Delete</button>&nbsp;&nbsp;
                    <button class="btn amber modal-trigger" href="#modal2" onClick={()=>{current_recipe_id.current = elem.id; settitle(elem.title); setdesc( elem.description)}}>Edit</button>
                    </div>
                </div>
                </div>
            </div>;
            return item;
        }
        var x = recipeList.map(card)
        setlist(x);
    },[recipeList])
    
    return (
        <>
       {list}
            


            <div id="modal2" class="modal" style={{width:"600px"}}>
                <div class="modal-content">
                    <h4>Edit Recipe</h4>
                    <div class="row">
                        
                            <label>Recipe Name</label>
                            <input type="text" value={title} onChange={(e)=>{settitle(e.target.value)}} />
                        
                    </div>
                    <div class="row">
                    <label for="textarea1">Description</label>
                        <textarea value={description } onChange={(e)=>{setdesc(e.target.value)}} class="materialize-textarea"></textarea>
                        
                        
                    </div>
                </div>
                <div class="modal-footer">
                <a class="modal-close blue waves-ef btn-flat" onClick={EditAPI}>Save Changes</a>
                </div>
            </div>
        </>
    )
}

export default Recipe
