import { takeEvery,call,put} from 'redux-saga/effects';
import axios from 'axios';

function* getRecipies() {
    try {
      // data is obtained after axios call is resolved
      let result = yield call(() =>  axios.get('https://recipe-app-0002.herokuapp.com/get/recipe'));
      let res= result.data.recipies;
     
      // dispatch action to change redux state
      yield put({type:"REFRESH_R" , payload :res });
     
    } catch (e) {
      // catch error on a bad axios call
      console.log(e);
    }
  }

//watcher function - watches for actions
export  function* watchRecipies() {
    console.log("watcher function Recipe")
    yield takeEvery('REFRESH', getRecipies);
    //yeild takeEvery('REFRESH);
    //call(getCO);
}