
const initState = {
    recipies :[]
}

const rootReducer = (state = initState, action) => {
  console.log(action.type)
  if(action.type === 'REFRESH_R')
    {
        var refreshedRecipes = action.payload;
        return {
            ...state,
            recipies : refreshedRecipes,
        }
    }
    return state;
}

export default rootReducer;